<template>

  <div class="back">
     <div align = "center" ><h2> {{name}}  My Page Update</h2>
     <h3> <a href="#" @click="logout">  </a>
     <button type="button" class="btn btn-lg btn-danger"  @click.prevent="withdrawal"></button>
     </h3>
     </div>
      <div class="sub">
        <div class="pull-left size" >
             <legend><h3> </h3></legend>
           <span><input v-model="passwd" type="text" name="newpwd" size="20" placeholder="  " style="float: none;width: 70%;"></span>
                <button style="float: right;" @click="update"></button><br/><br/><br/>
                </div>
         <div class="pull-right size" >        
              <legend><h3>  </h3></legend>
                                :<input v-model="hak" type="text"  placeholder="  " > <br />
                                :<input v-model="ban" type="text"  placeholder="  ">  <br />
                                :<input v-model="score" type="text"  placeholder="  " >
             <br/><br/><button @click.prevent="update"></button><br/><br/>
        </div>
      </div>
    </div>
</template>

<script>
import axios from 'axios'
import {store} from '../../store'
export default {
  data(){
    return{
      context:'http://localhost:8080',
      id: store.state.person.id,
      userid: store.state.person.userid,
      passwd: store.state.person.passwd,
      name: store.state.person.name,
      birthday: store.state.person.birthday,
      gender: store.state.person.gender,
      hak:store.state.person.hak,
      ban:store.state.person.ban,
      score: store.state.person.score
    }
  },
  methods:{
    logout(){
      alert('')
      alert(store.state.person.name)
      store.state.person={}
      alert(store.state.person.name)
      this.$router.push({path: '/login'})

    },
    withdrawal(){
      alert('')
      axios
      .delete(`${this.context}/withdrawal/${store.state.person.userid}`)
      .then(
        alert(' 2')
      )
      .catch(()=>{
        alert('  ')
      })
      this.$router.push({path:'/'})
    },
    update(){
      alert(' ')
      let url = `${this.context}/update/${store.state.person.userid}`
      let data = {
        userid: store.state.person.userid,
        hak: store.state.person.hak,
        ban: store.state.person.ban,
        score: store.state.person.score
      }
      let headers = {
          'authorization': 'JWT fefege..',
          'Accept' : 'application/json',
          'Content-Type': 'application/json'
      }
      axios
      .put(url, data, headers)
      .then(res=>{
        alert(res.data)
      })
      .catch(()=>{
        alert(' 3')
      })
    }
  }
}
</script>

 <style scoped>
    .back {width: 700px; margin: 0 auto;}
    .sub {width: 90%; height: 600px; margin: 0 auto; text-align: center;}
    .pull-left {float: left;}
    .pull-right {float: right;}
    .top {margin-top: 1%;}
    .rt {margin-right: 1%;}
    .size {width: 49.5%; height: 100%;}
</style>
