<template>
<div>
	<fut-head :style="`height: ${height}vh`" :propImg="stadiumImg"/>
  <v-card class="card">
    <v-card-title><h1>
      <router-link :to="{name: 'futsalstadium', params: {stadiumName: selectMatch.stadiumname}}">
        {{selectMatch.stadiumname}}</router-link>
    </h1></v-card-title>
    <v-card-subtitle>{{selectMatch.stadiumaddr}}<br/>{{timeToDate}}</v-card-subtitle>
    <v-card-action>
      <v-chip outlined @click="linkCopy"></v-chip>
      <v-chip outlined></v-chip>
      <v-chip outlined></v-chip>
    </v-card-action>
    <v-card-text>{{selectMatch.stadiumname}} {{timeToDate}}  
        <code>{{success}}%</code>    .
    </v-card-text>
  </v-card>
  <v-card class="card">
    <v-card-title></v-card-title>
    <v-card-subtitle>{{selectMatch.difficulty}}        .</v-card-subtitle>
    <v-row class="justify-center pa-1">
      <v-col
        v-for="(n) of matchRule"
        :key="n" cols="2">
        <v-card>
          <v-img :src="require(`@/assets/img/matchRule/${
            ['4','5','6'].includes(n) ? n+'vs'+n : n }.svg`)"/>
          <v-card-text class="text-center">{{msgSwitch(n)}}</v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-card>
  <v-divider/>
  <v-card class="card">
    <h3> </h3>
    <v-row class="justify-center pa-1">
      <v-col
        v-for="n of selectMatch.stadiumfacility.split('\,')"
        :key="n" cols="2">
        <v-card>
          <v-img :src="require(`@/assets/img/stadium/${n}.svg`)"/>
        </v-card>
      </v-col>
    </v-row>
  </v-card>
  <v-card class="card">
    <h4> </h4>
    <li justify-left 
      v-for="item of stadiumText" :key="item">
      {{item}}
    </li>
  </v-card>
  <v-divider/>
  <v-card class="card">
    <div class="d-flex">
      <v-img elevation-6 src="https://pds.joins.com/news/component/htmlphoto_mmdata/201910/26/ce877ed2-0800-457f-b9a6-a86044718d40.jpg"/>
      <div>
        <v-card-title>  : {{selectMatch.adminname}}</v-card-title>
        <v-card-subtitle>      {{selectMatch.adminname}}. 
                          . 
                  !</v-card-subtitle>
      </div>
    </div>
  </v-card>
  <v-divider/>
  <v-card class="card">   
    <ul>
      <h2>-  -</h2>
      <span>      .</span>
      <li>     .</li>
      <li>     .</li>
      <li> ,   .</li>
      <li> ,       .</li>
      <li>,        .</li>
    </ul>
    <ul>
      <h4>   </h4>
      <li>  1 30       </li>
      <li>      </li>
      <li>1    , 2     .</li>
    </ul>
    <v-divider/>
    <ul>
      <h2>- / -</h2>
      <h4>  </h4>
      <li>    (6 vs 6  10 / 5 vs 5  8)             1 30      .</li>
      <li>          .</li>
      <li>              .</li>
      <li>         .</li>
    </ul>
    <ul>
      <h4>  </h4>
      <li>          .</li>
      <li>     .</li>
      <li> 1 30        .</li>
      <li>        .</li>
    </ul>
    <ul>
      <h4> </h4>
      <li> 2   :  </li>
      <li> 1   :  80% </li>
      <li>  3   :  20% </li>
      <li>  3   : 0%</li>
      <li>      1 30            .</li>
      <li>       .</li>
    </ul>
  </v-card>
  <v-btn @click="payment()" id="floatdiv" pa-3 fab x-large block rounded>   </v-btn>
</div>
</template>

<script>
import {store} from '@/store'
import FutHead from './FutHead'
export default {
  components:{FutHead},
  data(){
    return {
      height: 30,
      success: 100,
      selectMatch: store.state.selectMatch,
      matchRule: [
          store.state.selectMatch.num,
          store.state.selectMatch.gender,
          store.state.selectMatch.difficulty,
          store.state.selectMatch.shoes,
          'minmax'
        ],
      stadiumText: [
        `${store.state.selectMatch.num}vs${store.state.selectMatch.num}
                       ${parseInt(store.state.selectMatch.num)*2 -2}.`,
        ` ${store.state.selectMatch.num}     .`,
        ' :  2  /  ',
        '(      , 2     )',
        '1  ',
        '    '
      ],
      temp: ''
    }
  },
  computed: {
    stadiumImg(){
    return this.selectMatch.stadiumimg.split(',')
      .map(i => require(`@/assets/img/stadium/${i}.jpg`))
    },
    timeToDate(){
      const time = new Date(this.selectMatch.time)
      return `${time.getFullYear()} ${time.getMonth()+1} ${time.getDate()} 
      ${["","","","","","",""][time.getDay()]} ${time.getHours()}:00`
    },
  },
  methods: {
    linkCopy(){
      alert('  ')
      return ' '
    },
    msgSwitch(item){
      switch(item){
        case 1 : return ' '
        case 2 : return ' '
        case 3 : return ' '
        case '4' : return '4vs4'
        case '5' : return '5vs5'
        case '6' : return '6vs6'
        case 'shoes0' : return ' '
        case 'shoes1' : return ' '
        case 'minmax' : return `${this.selectMatch.num*2 - 2} ~ ${this.selectMatch.num*2 + 4}`
        default : return item
      }
    },
    payment(){
      if(store.state.user.name == undefined){
        alert(' ')
      }else{
        alert('')
        this.$router.push({name: 'futsalhome'})
      }
    }
  }
}
</script>
<style scoped>
.card{
  width: 80%;
  padding: 4px;
  text-align: left;
}
#floatdiv{

}
</style>