<template>
<div style="padding:0.5%;">
  <v-card style="float:left;">
  <v-card-title>  <v-btn @click="fillData()" color:blue>   </v-btn></v-card-title>
  <v-card-subtitle>    </v-card-subtitle>
  
      <v-hover v-slot:default="{ hover }">
        <v-card
          :elevation="hover ? 12 : 2"
          class="mx-auto"
          style="float:right"
          width="1100px"
        >
        <bar-chart :chart-data="datacollection"></bar-chart>
        
        </v-card>
        
      </v-hover>
        <br /><br />
        <v-row>
        <v-hover v-slot:default="{ hover }">
        <v-card
          :elevation="hover ? 12 : 2"
          class="mx-auto"
          height="450"
          max-width="350"
          style="float:left"
        >
        <line-chart :chart-data="ager"></line-chart>
        </v-card>
        </v-hover>
  <v-hover v-slot:default="{ hover }">
        <v-card
          :elevation="hover ? 12 : 2"
          class="mx-auto"
          height="450"
          max-width="350"
          style="float:left"
        >
  <pie-chart :chart-data="gender"></pie-chart>
  </v-card>
        </v-hover>
        </v-row>
  </v-card>
    
</div>
</template>

<script>
  import BarChart from '@/chart/BarChart.js'
  import LineChart from '@/chart/LineChart.js'
  import PieChart from '@/chart/PieChart.js'
  export default {
    components: {
      BarChart,
      LineChart,
      PieChart
    },
    data () {
      return {
        datacollection: null,
        gender:null,
        ager:null
      }
    },
    mounted () {
      this.fillData()
    },
    methods: {
      fillData () {
        this.datacollection = {
          labels: ['', '','','', '', '', ''],
          datasets: [
            {
              label: ' ',
              backgroundColor: '#FF1744',
              data: [this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt()]
            }, {
              label: ' ',
              backgroundColor: '#FF6D00',
              data: [this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt()]
            }, {
              label: '  ',
              backgroundColor: '#FFD600',
              data: [this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt()]
            }, {
              label: ' ',
              backgroundColor: '#64DD17',
              data: [this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt()]
            }, {
              label: ' ',
              backgroundColor: '#0091EA',
              data: [this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt()]
            }
          ]
        },
        this.gender = {
          labels:[' ', ''],
          datasets: [
            {
              label: '',
              backgroundColor: ['#4DD0E1','#FF4081'],
              data: [this.getRandomInt(), this.getRandomInt()]
            }]
        },
        this.ager={
          labels:['10 ', '20', '30', '40'],
          datasets: [
            {
              label: '',
              backgroundColor: ['#EA80FC'],
              data: [this.getRandomInt(), this.getRandomInt(), this.getRandomInt(), this.getRandomInt()]
            }
          ]
        }
      },
      getRandomInt () {
        return Math.floor(Math.random() * (50 - 5 + 1))
      }
    }
  }
</script>

<style>
  .small {
  }
</style>